.block {
    display: block;
}

.flex-centre{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.inline-block{
    display: inline-block;
}

.flex-around{
    display: flex;
    justify-content: space-between;
}

.flex-horizontal{
    display: flex;
    justify-content: center;
}

.overflow-y{
    overflow: hidden;
    overflow-y: scroll;
    height: 60vh;
    border: solid 1px;
    top: 10vh;

}