@media only screen and (max-width: 600px) {
    
    ul {
        padding: 0px;
    }

    li {
        list-style: none;
        display: block;
        padding: 10px;
    }

    .nav{
        position: absolute;
        transition: all 0.5s ease-out;
        background-color: #011111;
        height: 100%;
        width: 75%;
        top: 0px;
        z-index: 1;
    }

    .on {
        left: 0px;
    }

    .off {
        left: -80%;
    }

    .flex-center{
        display: flex;
        justify-content: center;
    }
    .Img{
        position: absolute;
        z-index: 2;
        top: 8px;
        left: 8px;
    }
    .overlay{
        position: absolute;
        z-index: 1;
        height: 100%;
        width: 100%;
        top: 0px;
        left: 0px;
        background-color: grey;
        transition: opacity 0.4s ease-out;
    }

    .layOn{
        z-index: 1;
        opacity: 0.6;
    }
    .layOff{
        z-index: -1;
        opacity: 0;
    }
}